
* {
  box-sizing: border-box;
}
html {
  height: auto;
}
body {
  height: 100%;
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin: 20px 0;
}
h1, h2, h3, h4, p {
  margin-top: 0;
}
.container {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 25px;
}
.logo {
  color: #e0822c;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 6vw;
  text-transform: uppercase;
  left: 30px;
  top: 20px;
  opacity: 0.6;
  text-align: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  border-bottom: solid 10px #e0822c;
}
img {
  max-width: 100%;
}
.page-title {
  font-size: 28px;
  letter-spacing: -1px;
  text-align: left;
  margin-bottom: 35px;
}
.list--unstyled {
  list-style: none;
  padding: 0;
}
.covers {
  display: -ms-grid;
  display: grid;
  grid-row-gap: 30px;
  grid-column-gap: 30px;
}
@media (min-width: 720px) {
.covers {
      -ms-grid-columns: (1fr)[2];
          grid-template-columns: repeat(2, 1fr);
}
}
@media (min-width: 1100px) {
.covers {
      -ms-grid-columns: (1fr)[3];
          grid-template-columns: repeat(3, 1fr);
}
}
.film-title {
  background: #000;
  color: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 0.5rem 1rem;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  text-align: center;
  transition: -webkit-transform 0.4s 0.7s;
  transition: transform 0.4s 0.7s;
  transition: transform 0.4s 0.7s, -webkit-transform 0.4s 0.7s;
  z-index: 10;
  width: 100%;
}
.is-correct .film-title {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}
.cover-img {
  margin-bottom: 5px;
  position: relative;
  overflow: hidden;
}
.cover-img::after {
    content: 'Correct!';
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: -webkit-transform 0.4s 0.2s;
    transition: transform 0.4s 0.2s;
    transition: transform 0.4s 0.2s, -webkit-transform 0.4s 0.2s;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background: rgba(88, 158, 83, 0.8);
    color: #fff;
    z-index: 5;
}
.is-correct .cover-img::after {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}
form {
  transition: 0.8s opacity ease 0.6s;
}
.is-correct form {
    opacity: 0.3;
}
input[type=text] {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  display: block;
  font-size: 15px;
  padding: 10px;
  width: 100%;
  border: solid 1px #ccc;
}
.is-wrong input[type=text] {
    border-color: red;
    -webkit-animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
            animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    outline: none;
}
.btn {
  border: 0;
  cursor: pointer;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 16px;
  padding: 10px;
  text-decoration: none;
}
.btn--primary {
  background: #e0822c;
  color: #fff;
}
.btn--check {
  background: #333;
  color: #fff;
  font-size: 10px;
  letter-spacing: 2px;
  text-transform: uppercase;
}
.btn--lg {
  font-size: 20px;
  padding: 10px 20px;
}
.btn--block {
  display: block;
  width: 100%;
}
@-webkit-keyframes shake {
10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
}
20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
}
30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
}
40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
}
}
@keyframes shake {
10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
}
20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
}
30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
}
40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
}
}



